$(document).ready(function () {
  var count = 0;

  function play() {
    $('.elem').on('click', function () {
      if ($(this).html() == "") {
        count++;
        if (count >= 9) {
          $('.btn').addClass('hover');
        }
        if (count % 2 == !0) {
          $(this).html("X");
          whowin();
        } else {
          $(this).html("0");
          whowin();
        }
      } else {
        return false;
      };
    });
  }


  var col1 = $('.elem:nth-of-type(1)')
  var col2 = $('.elem:nth-of-type(2)')
  var col3 = $('.elem:nth-of-type(3)')
  var d1 = $('.d1')
  var d2 = $('.d2')

  function whowin() {
    $('.row').each(function (index) {
      var field = $(this).children();
      if (field.text() == 'XXX' || field.text() == '000') {
        $(this).addClass('win');
        $('.elem').unbind("click");
        $('.btn').addClass('hover');

      }
    })
    if (col1.text() == 'XXX' || col1.text() == '000') {
      col1.addClass('win');
      $('.elem').unbind("click");
      $('.btn').addClass('hover');
    } else if (col2.text() == 'XXX' || col2.text() == '000') {
      col2.addClass('win');
      $('.elem').unbind("click");
      $('.btn').addClass('hover');
    } else if (col3.text() == 'XXX' || col3.text() == '000') {
      col3.addClass('win');
      $('.elem').unbind("click");
      $('.btn').addClass('hover');
    } else if (d1.text() == 'XXX' || d1.text() == '000') {
      d1.addClass('win');
      $('.elem').unbind("click");
      $('.btn').addClass('hover');
    } else if (d2.text() == 'XXX' || d2.text() == '000') {
      d2.addClass('win');
      $('.elem').unbind("click");
      $('.btn').addClass('hover');
    }
  }



  $('.btn.start').on('click', function () {
    count = 0;
    play();
    $(this).removeClass('start');
    $(this).text('restart');
  });


  $('.btn').on('click', function () {
    $('.elem').empty();
    $('.elem').removeClass('win');
    $('.btn').removeClass('hover');
    $('.row').removeClass('win');
    count = 0;
    play()
  });
});
